@charset "utf-8";

.mainSliderSection {width:100%; overflow:hidden; background-image:url("https://web-cf-image.cjenm.com/public/resources/images/common/dark-bg.gif");}
.mainSliderSection .sectionInner {max-width:1240px; margin:0 auto; padding:0 48px;}
.mainSliderSection .mainSliderWrap, .mainSliderSection .mainSliderBody {position:relative;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide {transition:width 0.6s cubic-bezier(0.6, 0, 0.4, 1);}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide, .mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .swiper-slide {height:49.21875vw; width:90px; margin-right:24px;}
.mainSliderSection .mainSliderBody .mainSliderMax .imgBox, .mainSliderSection .mainSliderBody .mainSliderArea .imgBox {position:relative; background:none; padding-bottom:56.2%; width:93.3333vw;}
.mainSliderSection .mainSliderBody .mainSliderMax .imgBox img, .mainSliderSection .mainSliderBody .mainSliderArea .imgBox img {position:absolute; top:0; left:0; width:100%;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide::after, .mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .swiper-slide::after {content:''; display:block; position:absolute; left:0; top:0; right:0; bottom:0;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide .clone {z-index:1;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide.swiper-slide-active .clone {opacity:0;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide img {margin-left:-50%; transition:all 0.55s cubic-bezier(0.6, 0, 0.4, 1);}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container, .mainSliderSection .mainSliderBody .mainSliderArea .swiper-container {overflow:visible;}
.mainSliderSection .mainSliderBody .mainSliderMax .imgBox > div, .mainSliderSection .mainSliderBody .mainSliderArea .imgBox > div {display:block; height:100%; position:absolute; left:0; top:0; right:0; bottom:0; z-index:2;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide, .mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .swiper-slide {overflow:hidden;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide.swiper-slide-active img {margin-left:0;}
.mainSliderSection .mainSliderBody .mainSliderMax .imgBox, .mainSliderSection .mainSliderBody .mainSliderArea .imgBox {width:1334px;}
.mainSliderSection .mainSliderBody .mainSliderMax .swiper-container .swiper-slide.swiper-slide-active, .mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .swiper-slide.swiper-slide-active {width:100%;}
.mainSliderSection .mainSliderBody .mainSliderArea {position:absolute; top:0; left:0; width:100%;}
.mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .swiper-slide {opacity:0;}
.mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .button-prev,
.mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .button-next {position:absolute; top:50%; margin-left:32px; margin-top:-24px; width:48px; height:48px; border-radius:50%; cursor:pointer; background-color:rgba(0,0,0,.6); z-index:15; transform:translateY(-50%);}
.mainSliderSection .mainSliderBody .mainSliderArea .swiper-container .button-next {right:32px;}

.mainSliderSection .swiper-pagination-custom {position:absolute; bottom:32px; right:32px; left:auto; display:inline-block; padding:4px 16px; width:auto; background:rgba(0,0,0,.7); border-radius:16px; z-index:10;}
.mainSliderSection .swiper-pagination-custom span {color:#fff;}
.mainSliderSection .swiper-pagination-custom span ~ span {padding-left:20px;}
.mainSliderSection .swiper-pagination-custom i {position:relative; left:10px; top:2px; background:#fff; display:inline-block; width:1px; height:17px; transform:rotate(20deg);}

@media screen and (max-width: 1024px){
	.mainSliderSection .mainSliderBody .mainSliderMax .imgBox, .mainSliderSection .mainSliderBody .mainSliderArea .imgBox{width:93.3333vw;}
}